.table {
    width: 600px;
    border-collapse: collapse;
    text-align: center;
  }
  .tr {
    height: 40px;
    border-bottom: 1px solid #ccc;
  }
  .tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  .td {
    text-align: left;
    padding-left: 15%;
  }
  .th {
    background: #3498db;
    color: #fff;
  }