.profile {
    background-color: rgb(255, 255, 255);
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
  }

  .description {
    text-align: center;
    padding-bottom: 30px;
  }

  .avatar {
    width: 100px;
    border-radius: 100%;
  }

  .name {
    font-size: 24px;
    font-weight: 700;
  }

  .tag {
    color: #9e9fa3;
  }

  .location {
    color: #9e9fa3;
  }

  .stats {
    background-color: #f4faff;
    width: 100%;
    height: 80px;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }

  .stats li {
    flex-basis: 100%;
    border: 1px solid #e5eaff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  .label {
    color: #a9acbb;
  }
  
  .quantity {
    font-weight: 700;
  }