.list {
    padding: 0;
    margin: 0;
    position: relative;
  }
  
  .item {
    list-style: none;
    display: flex;
    align-items: center;
    box-shadow: 0px 2px 4px 0px rgb(119, 116, 116);
    width: 300px;
    gap: 5px;
  }

  .avatar {
    margin-left: 40px;
    background-color: rgb(219, 226, 219);
    border-radius: 10px;
    padding: 10px;
  }

  .status {
    position: absolute;
    left: 15px;
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 100%;
  }

  .online {
    background-color: green;
  }

  .offline {
    background-color: red;
  }
  
  .name {
    font-size: 24px;
    font-weight: 500;
  }